
.bg-primary {
    background-color: #474B4F !important;
}

.bg-inverse {
    background-color: #ffd600 !important;
}

.text-primary {
    color: #ffd600 !important;
}

.text-inverse {
    color: #474B4F !important;
}

.btn-primary {
    background-color: #474B4F !important;
    color: #ffd600 !important;
    border-color: #474B4F !important;
}

.btn-secondary {
    background-color: initial;

}

.btn-secondary.active {
    background-color: rgba(147, 191, 0, 0.35);
    border-color: rgba(145, 189, 0, 0.35);
}

a.text-primary:hover,
.btn-primary:hover {
    color: #fcfcfc !important;
}

.navbar-brand.text-primary:hover {
    color: #ffd600 !important;
}

.btn {
    cursor: pointer;
    border-radius: 0;
}

.btn:focus,
.btn:active:focus {
    outline: 0;
    box-shadow: none;
}

.navbar-brand {
    font-family: 'Amatic SC', cursive;
    font-size: 3.5rem;
    font-weight: 700;
    line-height: initial;
}

.navbar-toggler,
.navbar-toggler:focus {
    outline-color: #ffd600;
}

.navbar-toggler-icon {
    background-color: #474B4F;
}

span.navbar-toggler-icon {

    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#ffd600' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

/***************************************************************************************/
body, html {
    height: 100%;
}

body {
    -webkit-overflow-scrolling: touch;
}

/*******************************Header navbar*******************************************/
header {
    height: 4.5rem;
}

header nav {
    height: 4.5rem;
}

.name-holder img {
    height: 50px;
    width: 50px;
}

header .btn-primary {
    line-height: 1.5;
}

header .navbar-toggler-right {
    position: fixed;
    top: 1rem;

}

header .navbar-collapse.collapsing,
header .navbar-collapse.collapse.show {
    visibility: visible;
    position: fixed;
    top: 4rem;
    right: 15px;
    width: 200px;
    min-width: 200px;
    background-color: #474B4F
}

legend {
    padding-bottom: 5px;
    border-bottom: 1px solid #ffd600;
}

/*******************************Main**************************************************/
main {

    position: relative;
    top: 72px
}

/******************************Aside navbar******************************************/
aside {
    min-height: 100vh;
    max-width: 101px;
    min-width: 101px;
}

aside.mobile {
    max-width: 70px;
    min-width: 70px;
}



.sidebar-nav > ul > .nav-item {
    width: 100%;
    height: 101px;
}

.sidebar > .btn {
    display: inline-block;
    height: 101px;
    width: 101px;
}

.sidebar.mobile > .btn {
    height: 70px;
    width: 70px;
    font-size: .7rem;

}

.btn-aside {
    background: transparent;
    color: #ffd600;
    border: 2px solid #474B4F;

}

.btn-aside:hover {
    color: #ffd600;
    border: 2px ridge #ffd600;
}

.active .btn-aside {
    background-color: #ffd600;
    color: #474B4f;
    border-color: #ffd600;
}

.active .btn-aside:hover {
    color: #474B4f;
    border: 2px ridge #474B4f;
}

/*************************Page content******************************************************/
.page-content {
    min-height: 100vh;
    width: calc(100% - 100px);

}

.page-content.mobile {
    left: 70px;
    width: calc(100% - 70px);
}

.page-content.portrait {
    width: 100%;
    left: 0;
    padding-bottom: 80px;
}

/*.page-content.mobile {*/
/*left: 70px;*/
/*width: calc(100% - 70px);*/

/*}*/

@media (max-width: 575px) {
    .page-content.iphone5 {
        left: 70px;
        width: calc(100% - 70px);
    }

    .page-content {
        left: 0;
        width: 100%;
    }

}

/*******************************Todo card calendar*************************************************/
/*calendar*/
.calendar-wrapper {
    border: 0.1em solid #ccc; /* wrap the calendar inside a box */
    width: 11em; /* define a width for the box */

    box-shadow: 0.1em 0.2em 0.8em #ccc; /* box shadow for better view */
}

.calendar-header {
    background-color: #3f51b5; /* give the header a background color */
    color: white; /* give the header a text color */
    text-align: center; /* center the text inside the header */

}

.calendar-body .col-xs-1 {
    width: 1.5em; /* give each column a fixed width */
    text-align: center; /* align text in the center */
}

.dates, .weekdays {
    padding: 0.2em 0.5em; /* giving dates some space  */
}

.calendar-body p {
    font-size: 12px;
    margin: 0.25rem 0;
}

.calendar-body p.disabled {
    color: #4b5257;
}

.calendar-body div.selected {
    background-color: rgba(255, 214, 0, 0.39);
}

/*************************Footer*******************************************************************/
footer {
    height: 70px;
}

footer .sidebar > .btn {
    height: 80px;
    width: 80px;
    padding: .5rem 0.25rem;
}

/*footer ul.nav {*/
/*width: 320px;*/

/*}*/
footer .sidebar > .btn {
    height: 80px;
    width: 80px;

}

@media (min-width: 768px) {
    aside .nav {
        position: fixed;
        top: 4.5rem;
        left: 0;
        bottom: 0;
    }
}

