.btn-border {
    border-radius: 25px 0 25px 25px;
}

.btn-primary-custom {
    width: 200px;
    font-size: 2rem;
}

.btn-help {
    width: 200px;
    height: 200px;
    font-family: 'Neucha', cursive;
    letter-spacing: 1.5px;
    font-size: 38px;
    word-spacing: 5px;
}

