.label-btn {
    height: 50px;
    width: 50px;
}

.btn.label-btn:hover {
    border: 2px ridge #ffd600;
}

.btn.label-btn:focus,
.btn.label-btn.focus,
.btn.label-btn:active:focus,
.btn.label-btn.active:focus {
    box-shadow: 0 0 0 2px #ffd600;
}

.label-btn > .fa-check {
    color: #474B4F;
}

#in-outdoor > .label-btn, #payment > .label-btn, #canHelpType > .label-btn {
    width: initial;
}

.mark-checked {
    color: #ffd600 !important;
}

.my-label {
    height: 1rem;
    color: #177611;
    font-size: 0.75rem;

}

.date-field {
    height: 305px;
}

.spinner {
    width: 100px;
}

/*************************result card****************************************/
.user-dash {
    background-color: rgba(255, 214, 0, 0.39);
}

.executor {

    background-color: rgba(147, 191, 0, 0.35);
}

.user-dash .btn,
.executor .btn {
    color: #474B4F;
    background-color: inherit;
}

.executor .btn {
    cursor: pointer;
}

.executor .btn:hover,
.user-dash .btn:hover {
    color: #474B4F;
    background-color: rgba(136, 175, 47, 0.63);
}

.card-aside {
    width: 11em;
    /*max-width: 225px;*/
}

.card-aside ul li {
    background-color: rgba(147, 191, 0, 0.06);
    border: 1px solid rgba(147, 191, 0, 0.35);
    margin: 1px 0;
}

.card-aside ul li > a,
.card-aside ul li > a:not([href]),
.card-aside ul li > a:not([href]):not([tabindex]):hover {
    color: #474B4F;
    line-height: 37px;
}

.card-aside ul li > a:hover,
.card-aside ul li > a:not([href]):not([tabindex]):hover {
    background-color: rgba(255, 214, 0, 0.34);
}

.card-aside ul li > a.active,
.card-aside ul li > a:not([href]):not([tabindex]).active,
.card-aside ul li > a:not([href]):not([tabindex]).active:hover {
    background-color: rgba(147, 191, 0, 0.35);
}

.card.card-main {
    border: 1px solid rgba(147, 191, 0, 0.35);
}

.card-header.other-user {
    background-color: rgba(147, 191, 0, 0.35);
}

.own-message .card-header {

    background-color: rgba(121, 128, 135, 0.16)
}

.card-header .btn {

    color: #474B4F;
    cursor: pointer;
}

.card-header .btn:hover {
    background-color: rgba(147, 191, 0, 0.44);
}

.messages {
    height: 400px;
    overflow-y: scroll;
}

/*******************popover, popover-recommendations***********************************/

.userpic-info {
    height: 32px;
    width: 32px;
    -webkit-transition: all 0.7s; /* For Safari 3.1 to 6.0 */
    transition: all 0.7s ease;

}

.userpic-info img {
    padding: 5px;

    transition: transform 1s ease;

}

.userpic-info.inlarge {
    height: 200px;
    width: 300px;
    background-color: rgb(123, 122, 130);
    position: absolute;
    top: -5px;
    left: -10px;
    z-index: 1000;
    padding: 10px;

}

.userpic-info.inlarge > .user {
    width: 75px;
    height: 75px;
    border: 1px solid red;
}

.userpic-info.inlarge > .popover-username {
    width: 200px;
    border: 1px solid black;
}

.userpic-info.inlarge.no-comments {
    height: 100px;
}

.userpic-info img.userpic-inlarge {
    transform: scale(2) translate(10px, 10px);
}

.userpic-info .comment {
    color: #fcfcfc;
}

.userpic-wrap {
    position: relative;
    height: 37px;
    width: 37px;
}

.popover-content table td {
    border: none;
}

.rating-popover {
    width: 250px;
}

.rating-popover a {
    line-height: 1rem;
}

.profile-card-fixed {
    width: 200px;
}

.profile-card-fixed .photo-holder {
    width: 150px;
    font-size: 14px;
}

.profile-card-fluid {
    width: calc(100% - 200px);
}

ul.profile-social-icons .disabled {
    color: rgba(128, 128, 128, 0.66)
}

.donut {
    width: 18px;
    height: 18px;
}

.donut.color {
    fill: deeppink;
}

#settings-nav {
    width: 140px;
    font-size: 18px;
}

#settings-nav ul {
    position: sticky;
    top: 6rem;
}

.profile .first-col {
    width: 150px;
}

.table.profile td {
    vertical-align: middle;
}

